
#sidebar {
    float: left;
    min-width: 250px;
    max-width: 250px;
    background: #1f2327;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

#sidebar ul{
     background: #FFF; 
}

#sidebar li a i {
    margin-right: 10px;
}

#sidebar li ul a {
    font-size: 0.8em !important;
    padding: 8px 4px 8px 30px;
}

#sidebar.active {
    min-width: 80px;
    max-width: 80px;
    text-align: center;
}

#sidebar.active li a {
    padding: 20px 10px;
    text-align: center;
    font-size: 0.85em;
 }

#sidebar.active li a i {
    margin-right: 0;
    display: block;
    font-size: 1.8em;
    margin-bottom: 5px;
}

#sidebar.active ul a {
    padding: 10px !important;
}

a[aria-expanded="false"]::before,
a[aria-expanded="true"]::before {
    content: '\e259';
    display: block;
    position: absolute;
    right: 20px;
    font-family: 'Glyphicons Halflings';
    font-size: 0.6em;
}

a[aria-expanded="true"]::before {
    content: '\e260';
}


/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */

@media (max-width: 768px) {
    #sidebar {
        min-width: 80px;
        max-width: 80px;
        text-align: center;
        margin-left: -80px !important;
    }
    a[aria-expanded="false"]::before,
    a[aria-expanded="true"]::before {
        top: auto;
        bottom: 5px;
        right: 50%;
        -webkit-transform: translateX(50%);
        -ms-transform: translateX(50%);
        transform: translateX(50%);
    }
    #sidebar.active {
        margin-left: 0 !important;
    }
    #sidebar .sidebar-header h3,
    #sidebar .CTAs {
        display: none;
    }
    #sidebar li a {
        padding: 20px 10px;
    }
    #sidebar li a span {
        font-size: 0.85em;
    }
    #sidebar li a i {
        margin-right: 0;
        display: block;
    }
    #sidebar ul a {
        padding: 10px !important;
    }
    #sidebar li a i {
        font-size: 1.3em;
    }
    #sidebar {
        margin-left: 0;
    }
    #sidebarCollapse span {
        display: none;
    }
}


